export function configRouter(router) {
    router.map({
        '/who-is-on-duty': {
            component: require('./tools/who-is-on-duty.vue'),
            title: '',
            desc: 'Yo'
        },
        '/take-picture-on-phone': {
            component: require('./code/take-picture-on-phone.vue'),
            title: '',
            desc: ' /  oss'
        },
        // not found handler
        '*': {
            component: {
                template: '<h1 style="margin-top:20px;text-align:center;color:#E9573F;"> ! &#128561;</h1>'
            }
        }
    })
}


